import React,{Component} from 'react';
import axios from 'axios';
import './RestDetails.css'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import Header from '../../Header';
import {Link} from 'react-router-dom'

const url ="https://novintern.herokuapp.com/rest"

class Details extends Component{
    constructor(){
        super()

        this.state={
            details:''
             
            
        }
    }


    render(){
        let {details} = this.state
        return(
            <>
            <Header/>
            <br/>
            <div className="container">
                
                <div className="panel panel-primary">
                    <div className="panel-heading text-center">
                        <h3>{this.state.details.name}</h3>
                    </div>
                    <div className="panel-body">
                        <div className="row">
                            <div className="col-md-12">
                                <img className="img-responsive" src={details.thumb}
                                style={{height:400,width:1500}}/>
                            </div>
                            <div className="col-md-6">
                                <div class="detailStyle">
                                <h3>{details.name}</h3>
                                <h3>&#8360;.{details.cost}</h3>
                                <h3>{details.locality}</h3>
                                <h3>{details.address}</h3>
                                </div>
                            </div>
                        </div>
                        <hr/>
                        <Tabs>
                            <TabList>
                                <Tab>OverView</Tab>
                                <Tab>Contact</Tab>
                            </TabList>

                            <TabPanel>
                                <h2>About This Place</h2>
                                <p>{details.name} is a great place to eat, combining an intriguing atmosphere with excellent, interesting food. The mission is not only to have great tasting food, but have efficient and friendly service because customer satisfaction is paramount. We want to be the restaurant choice for all families and singles, young and old, male or female. Employee welfare will be equally important to our success. Everyone will be treated fairly and with the utmost respect. We want our employees to feel a part of the success of Gabri’s Lounge and Restaurant. Happy employees make happy guests. </p>
                            </TabPanel>

                            <TabPanel>
                                <h2>Contact Us</h2>
                                <h3>{details.address}</h3>
                                <h3>Phone: 040-2269535</h3>
                            </TabPanel>
                        </Tabs>
                        <Link className='btn btn-danger' to="/">Back</Link> &nbsp;
                        <Link className='btn btn-success' to={`/booking/${details.name}`}> Proceed To Booking</Link>

                    </div>
                </div>
            </div>
            </>
        )
         
    
            
    }

    componentDidMount(){
        const hotelId=this.props.match.params.id
        sessionStorage.setItem('HotelID',hotelId)
        axios.get(`${url}/${hotelId}`)
        .then((res) => {this.setState({details:res.data[0]})}) 
    }
}

export default Details;