import React,{Component} from 'react';
import './currencyconverter.css'
 

class CurrencyConverter extends React.Component {
    constructor(props) {
      super(props);
      
      this.state = {
        baseCurrency:'INR',
        convertToCurrency:'',
        baseAmount:'100',
        rates: [],
        currencies: [],
        price:''
         
      };
      
      this.changeConvertToCurrency = this.changeConvertToCurrency.bind(this); 
      this.changeBaseAmount = this.changeBaseAmount.bind(this);
      this.getConvertedCurrency = this.getConvertedCurrency.bind(this);
      this.callAPI = this.callAPI.bind(this);
       
      
    }
    componentDidMount(){
    this.callAPI(this.state.baseCurrency)
    }
    
    
    changeBaseCurrency(event) {
      this.setState({ baseCurrency: event.target.value});
      this.callAPI(event.target.value)
      
    }
    
   callAPI(base) {
    
     const url = `https://api.exchangeratesapi.io/latest?base=${base}`;
      
      fetch(url)
      .then((res) => res.json())
      .then(data => 
        this.setState({rates: data['rates'],
        currencies: Object.keys(data['rates']).sort()}));
     
   } 
    
    
    changeConvertToCurrency(event) {
      this.setState({convertToCurrency: event.target.value});
    }
    
    changeBaseAmount(event) {
     this.setState({
       baseAmount:  event.target.value
     });
    }
    
    getConvertedCurrency(baseAmount, convertToCurrency, rates) {
        return Number(baseAmount * rates[convertToCurrency]).toFixed(2);
    }
     
    
    render() {
      
      const {currencies,rates,baseCurrency,baseAmount,convertToCurrency} = this.state;
      
      const currencyChoice = currencies.map(currency =>
        <option key={currency} value={currency}> {currency} </option>      
      );
                                            
      const result = this.getConvertedCurrency(baseAmount, convertToCurrency, rates);
      
      
      return(
        <>
            <div className="container"> 
            <div className="formContainer data">
                <form>
                    <h1>Currency Converter</h1>
                    <h4>Base Currency = {baseCurrency}</h4>
                    <select style={{height:'40px',width:'70%'}}   value={baseCurrency}>
                    {currencyChoice} 
                        <option>{baseCurrency}</option>
                    </select>
                    
                    <h4>Output Currency = {convertToCurrency}</h4>
                    <select style={{height:'40px',width:'70%'}} value={convertToCurrency} onChange={this.changeConvertToCurrency}>
                        {currencyChoice}
                    </select>
                    
                    <h3>Amount:</h3>
                    <input type='number' defaultValue={baseAmount} 
                            onChange={this.changeBaseAmount}>
                    </input>                             
                </form>                       
                <h2 className="result-text">{baseAmount} {baseCurrency} is equal to {result} {convertToCurrency}</h2>
            </div>
            </div>
        </>
      );
    }
    
  }
  export default CurrencyConverter;

  