import React,{Component} from 'react';
import Header from '../../Header';
import CurrencyConverter from './currencyconverter'
import axios from 'axios';
 
 

const BookingUrl = "https://novintern.herokuapp.com/placeorder"
const url = "https://novintern.herokuapp.com/rest"

class PlaceBooking extends Component{
    constructor(props){
        super(props)

        this.state={
            hotel_name:this.props.match.params.name,
            name:sessionStorage.getItem('username'),
            phone:'',
            address:'',
            price:''
        }
    }
    

    handleSubmit = () => {
        console.log(this.state)
        fetch(BookingUrl,
            {
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state)
            })
            .then(this.props.history.push('/viewBooking'))
    }

    handleChange=(event) => {
        this.setState({[event.target.name]:event.target.value})
         
    }
    
    
    

    render(){
        return(
            <>
            <Header/>
            <br/>
            <div className="container">

                <div className="panel panel-info">
                    <div className="panel-heading">
                        Place Booking
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>Hotel Name</label>
                            <input name="hotel_name" value={this.state.hotel_name} readOnly
                            className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Name</label>
                            <input name="name" value={this.state.name} placeholder="Enter Your Name"
                            className="form-control"
                            onChange={this.handleChange}/>
                        </div>
                        <div className="form-group">
                            <label>Phone</label>
                            <input name="phone" value={this.state.phone}
                            className="form-control" placeholder="Enter Your Phone"
                            onChange={this.handleChange}/>
                        </div>
                        <div className="form-group">
                            <label>Address</label>
                            <input name="address" value={this.state.address}
                            className="form-control" placeholder="Enter Your address"
                            onChange={this.handleChange}/>
                        </div>
                        <div className="form-group">
                            <label>Cost</label>
                            <input name="cost" value= {this.state.price}
                            className="form-control"/>
                        </div>
                        <CurrencyConverter/>
                         
                        <button className="btn btn-success"
                        onClick={this.handleSubmit}>
                            Place Booking
                        </button>
                    </div>
                     
                </div>
            </div>
            </>
        )
    }
    
    componentDidMount(){
        
        
        var restid=sessionStorage.getItem('HotelID')
        axios.get(`${url}/${restid}`)
        .then((res) =>{this.setState({price:res.data[0].cost})}) 
         
         
            
    }

}

export default PlaceBooking;