import React from 'react';
import './listing.css';
import {Link} from 'react-router-dom';


const ListingDisplay = (props) => {
    const renderList = ({restaurentList}) => {
        if(restaurentList){
            if(restaurentList.length>0){
                return restaurentList.map((item) => {
                    return(
                        <div className="box">
                            <div className="maindiv">
                                <div className="image">
                                    <img src={item.thumb} className="imageClass"/>
                                </div>
                                <div className="details">
                                    <h1><Link to={`/details/${item._id}`}>
                                        {item.name}
                                    </Link></h1>
                                    <p>&#8360; {item.cost} Per Two</p>
                                    <p>{item.Cuisine[0].name}</p> 
                                </div>
                            </div>
                            <hr/>
                            <div className="moredetails">
                                <h2>Address</h2>
                                <p>{item.locality}</p>
                                <p>{item.address}</p>
                            </div>
                        </div>
                    )
                })

            }else{
                return(
                    <center>
                        <h1>No Data Found..</h1>
                    </center>
                )
            }
            
        }else
        {
            return(
                <div>
                    <i class="fa fa-spinner fa-spin" style={{fontSize:'100px'}}></i>
                </div>
            )
        }
    }

    return(
        <>
        {renderList(props)}
         
        </>
    )
    
      
}

export default ListingDisplay;



