import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './listingDisplay';
import {Link} from 'react-router-dom';
import Header from '../../Header';
import CuisineFilter from '../filters/cuisinePerFilter' ;
import CostFilter from '../filters/CostPerFilter'
import SortFilter from '../filters/sortfilter'

const url = "https://novintern.herokuapp.com/rest?mealtype=";

class ListingApi extends Component{
    constructor(props){
        super(props)

        this.state={
            restlist:'',
        }
    }
      setDataPerFilter = (sortedData) => {
          this.setState({restlist:sortedData})
      }
    render(){
        console.log(">>>",this.state.restlist)
         
        return(
           <div className="row" >
                <Header/>
                <div style={{marginLeft:'5%'}}>

                    <div className="col-md-2" className="filter">
                        <h4>Filters</h4><br/>
                         <CuisineFilter restPerCuisine={(data) => {this.setDataPerFilter(data)}}/>
                         <CostFilter restPerCost={(data) => {this.setDataPerFilter(data)}} />
                         <SortFilter restPerSort={(data) => {this.setDataPerFilter(data)}}/>
                    </div>
                    <div className="col-md-10" className="first">
                        <ListingDisplay restaurentList={this.state.restlist}/>
                    </div>
                    <div class="pagination" style={{marginLeft:'40%'}}>
                    <a href="#">&laquo;</a>
                    <a href="#">1</a>
                    <a href="#">2</a>
                    <a href="#">3</a>
                    <a href="#">4</a>
                    <a href="#">5</a>
                    <a href="#">6</a>
                    <a href="#">&raquo;</a>
                </div>
                </div>
                
           </div>
       
           
        )
    }

    componentDidMount(){
        var mealid = this.props.match.params.id
        sessionStorage.setItem('mealId',mealid)
        axios.get(`${url}${mealid}`)
        .then((response) => {this.setState({restlist:response.data})})
    }

    
     
}


export default ListingApi;